import React from "react";
import { Link } from "gatsby";
import filler from '../images/filler.jpeg';


function Function (props) {
  const path = '/projects/' + props.url;

  return (
    <Link className='software-row' to={path}>
      <div className='software-img'>
        <img
          alt={props.name}
          src={props.src ? props.src : filler} />
      </div>
      <div className='software-row-text'>
        <p className='descriptor'>{props.descriptor}</p>
        <p className='award'>{props.award}</p>
        <h1 className='software-row-title'>
          {props.name}
        </h1>
      </div>
    </Link>
  );
}

export default Function;
